						<text class="product-desc">{{product.description}}</text>
						<view class="product-meta">
							<text class="product-sales">Â∑≤ÂîÆ{{product.sales}}‰ª∂</text>
							<text class="product-rating">‚≠ê{{product.rating}}</text>
						</view>
						<view class="product-price-section">
							<text class="product-price">¬•{{product.price}}</text>
							<text class="product-original-price" v-if="product.originalPrice">¬•{{product.originalPrice}}</text>
						</view>
					</view>
					<view class="product-actions">
						<button class="buy-btn" @click.stop="buyNow(product)">Á´ãÂç≥Ë¥≠‰π∞</button>
						<button class="cart-btn" @click.stop="addToCart(product)">
							<text class="cart-icon">üõí</text>
						</button>
					</view>
				</view>
			</view>
		</view>
		
		<!-- Ë¥≠Áâ©ËΩ¶ÊÇ¨ÊµÆÊåâÈíÆ -->
		<view class="cart-float" @click="viewCart" v-if="cartCount > 0">
			<text class="cart-icon">üõí</text>
			<view class="cart-badge">{{cartCount}}</view>
		</view>
		
		<!-- Á©∫Áä∂ÊÄÅ -->
		<view class="empty-state" v-if="filteredProducts.length === 0">
			<text class="empty-text">{{searchKeyword ? 'Êú™ÊâæÂà∞Áõ∏ÂÖ≥ÂïÜÂìÅ' : 'ËØ•ÂàÜÁ±ªÊöÇÊó†ÂïÜÂìÅ'}}</text>
		</view>
	</view>
</template>

<script>
