				selectedCategory: 'all',
				sortType: 'default',
				cartCount: 0,
				categories: [
					{ id: 'all', name: 'ÂÖ®ÈÉ®', icon: 'üè™' },
					{ id: 'vr', name: 'VRËÆæÂ§á', icon: 'ü•Ω' },
					{ id: 'gaming', name: 'Ê∏∏ÊàèËÆæÂ§á', icon: 'üéÆ' },
					{ id: 'accessories', name: 'ÈÖç‰ª∂', icon: 'üîß' },
					{ id: 'clothing', name: 'ÊúçË£Ö', icon: 'üëï' },
					{ id: 'drinks', name: 'È•ÆÂìÅ', icon: 'ü•§' },
					{ id: 'snacks', name: 'Èõ∂È£ü', icon: 'üçø' }
				],
				products: [
					{
						id: 1,
						name: 'VRÁúºÈïúÂ•óË£Ö',
						description: 'È´òÊ∏ÖVRÁúºÈïúÔºåÊ≤âÊµ∏Âºè‰ΩìÈ™å',
						price: 299,
						originalPrice: 399,
						image: '/static/image/day/VR‰ΩìÈ™åÈ¶Ü.jpg',
						category: 'vr',
						sales: 156,
						rating: 4.8,
						stock: 50
					},
					{
						id: 2,
						name: 'Ê∏∏ÊàèÊâãÊüÑ',
						description: 'Êó†Á∫øÊ∏∏ÊàèÊâãÊüÑÔºåÊîØÊåÅÂ§öÂπ≥Âè∞',
						price: 89,
						originalPrice: 129,
						image: '/static/componentIndex.png',
						category: 'gaming',
						sales: 234,
						rating: 4.6,
						stock: 100
					},
					{
						id: 3,
						name: 'VRÊâãÊüÑ',
						description: 'VR‰∏ìÁî®ÊâãÊüÑÔºåÁ≤æÂáÜÂÆö‰Ωç',
						price: 199,
						image: '/static/templateIndex.png',
						category: 'accessories',
						sales: 89,
						rating: 4.7,
						stock: 30
					},
					{
						id: 4,
						name: '‰ΩìÈ™åÈ¶ÜTÊÅ§',
						description: 'ËàíÈÄÇÈù¢ÊñôÔºåÊó∂Â∞öËÆæËÆ°',
						price: 59,
						originalPrice: 79,
						image: '/static/extuiIndex.png',
						category: 'clothing',
						sales: 67,
						rating: 4.5,
						stock: 200
					},
					{
						id: 5,
						name: 'ËÉΩÈáèÈ•ÆÊñô',
						description: 'ÊèêÁ•ûÈÜíËÑëÔºåË°•ÂÖÖËÉΩÈáè',
						price: 8,
						image: '/static/image/day/VR‰ΩìÈ™åÈ¶Ü.jpg',
						category: 'drinks',
						sales: 456,
						rating: 4.3,
						stock: 500
					},
					{
						id: 6,
						name: 'ÁàÜÁ±≥Ëä±',
						description: 'È¶ôËÑÜÂèØÂè£ÔºåËßÇÂΩ±ÂøÖÂ§á',
						price: 12,
						image: '/static/componentIndex.png',
						category: 'snacks',
						sales: 234,
						rating: 4.4,
						stock: 300
					},
					{
						id: 7,
						name: 'VRÂ§¥ÊòæÊîØÊû∂',
						description: 'ÂèØË∞ÉËäÇÊîØÊû∂ÔºåËàíÈÄÇ‰ΩìÈ™å',
						price: 45,
						image: '/static/templateIndex.png',
						category: 'accessories',
						sales: 123,
						rating: 4.6,
						stock: 80
					},
					{
						id: 8,
						name: 'Ê∏∏ÊàèËÄ≥Êú∫',
						description: 'Á´ã‰ΩìÂ£∞ËÄ≥Êú∫ÔºåÊ∏∏Êàè‰ΩìÈ™åÊõ¥‰Ω≥',
						price: 159,
						originalPrice: 199,
						image: '/static/extuiIndex.png',
						category: 'gaming',
						sales: 178,
						rating: 4.7,
						stock: 60
					}
				]
			}
		},
		computed: {
			selectedCategoryName() {
				const category = this.categories.find(c => c.id === this.selectedCategory);
				return category ? category.name : 'ÂÖ®ÈÉ®';
			},
			filteredProducts() {
				let result = this.products;
				
				// ÊåâÂàÜÁ±ªÁ≠õÈÄâ
				if (this.selectedCategory !== 'all') {
					result = result.filter(item => item.category === this.selectedCategory);
				}
				
				// ÊåâÊêúÁ¥¢ÂÖ≥ÈîÆËØçÁ≠õÈÄâ
				if (this.searchKeyword.trim()) {
					const keyword = this.searchKeyword.toLowerCase();
					result = result.filter(item => 
						item.name.toLowerCase().includes(keyword) ||
						item.description.toLowerCase().includes(keyword)
					);
				}
				
				// ÊéíÂ∫è
				result = this.sortProducts(result);
				
				return result;
			}
		},
		onLoad() {
			this.loadCartCount();
		},
		methods: {
			// Âä†ËΩΩË¥≠Áâ©ËΩ¶Êï∞Èáè
			loadCartCount() {
				const cart = uni.getStorageSync('cart') || [];
				this.cartCount = cart.length;
			},
			
			// ÊêúÁ¥¢ËæìÂÖ•
			onSearchInput() {
				// ÂÆûÊó∂ÊêúÁ¥¢ÔºåÂèØ‰ª•Ê∑ªÂä†Èò≤Êäñ
			},
			
			// ÊâßË°åÊêúÁ¥¢
			search() {
				console.log('ÊêúÁ¥¢ÂÖ≥ÈîÆËØç:', this.searchKeyword);
			},
			
			// ÈÄâÊã©ÂàÜÁ±ª
			selectCategory(category) {
				this.selectedCategory = category.id;
			},
			
			// ÊîπÂèòÊéíÂ∫è
			changeSort(type) {
				this.sortType = type;
			},
			
			// ÊéíÂ∫èÂïÜÂìÅ
			sortProducts(products) {
				switch(this.sortType) {
					case 'price':
						return products.sort((a, b) => a.price - b.price);
					case 'sales':
						return products.sort((a, b) => b.sales - a.sales);
					default:
						return products;
				}
			},
			
			// Êü•ÁúãÂïÜÂìÅËØ¶ÊÉÖ
			viewProductDetail(product) {
				uni.navigateTo({
					url: `/pages/shop/product-detail?product=${encodeURIComponent(JSON.stringify(product))}`
				});
			},
			
			// Á´ãÂç≥Ë¥≠‰π∞
			buyNow(product) {
				uni.navigateTo({
					url: `/pages/shop/checkout?product=${encodeURIComponent(JSON.stringify(product))}&type=buy_now`
				});
			},
			
			// Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶
			addToCart(product) {
				let cart = uni.getStorageSync('cart') || [];
				
				// Ê£ÄÊü•ÊòØÂê¶Â∑≤Âú®Ë¥≠Áâ©ËΩ¶‰∏≠
				const existingItem = cart.find(item => item.id === product.id);
				if (existingItem) {
					existingItem.quantity += 1;
				} else {
					cart.push({
						...product,
						quantity: 1
					});
				}
				
				// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
				uni.setStorageSync('cart', cart);
				
				// Êõ¥Êñ∞Ë¥≠Áâ©ËΩ¶Êï∞Èáè
				this.cartCount = cart.length;
				
				uni.showToast({
					title: 'Â∑≤Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶',
					icon: 'success'
				});
			},
			
			// Êü•ÁúãË¥≠Áâ©ËΩ¶
			viewCart() {
				uni.navigateTo({
					url: '/pages/shop/cart'
				});
			}
		}
	}
</script>

<style lang="scss">
